@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400:700&display=swap);
html[lang="ja"] {
  font-family: "Noto Sans JP", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.underline {
  border-bottom: 2px solid #00C0AB;
}

@media (max-width:768px) {
  .header_title {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: bold;
    font-size:32px;
    margin-top:48px;
  }

  .header_caption {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-size:12px;
    margin-top:16px;
  }
}



@media (min-width:769px) {
  .header_title {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: bold;
    font-size:64px;
    margin-top:48px;
  }

  .header_caption {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-size:16px;
    margin-top:16px;
  }
}

body {
  background-color:#001D1A;
  margin-bottom:100px;
}


.mode_switch {
  transition: opacity 0.2s;
  cursor: pointer;
  cursor: hand;
}

.mode_switch:hover {
  opacity: 0.6; 
}

.genre_intro {
  width:120px;
  border-radius:12px;
  background-color:rgba(255,255,255,0.25);
  border-width:0;
  margin-right:8px;
  transition: opacity 0.2s;
  height:36px;
  color:white;
  font-weight:bold;
  font-size:15px;
}

.genre_intro:hover {
  background-color:rgba(255,255,255,0.25);
  opacity:0.6
}

.genre_zoom {
  width:120px;
  border-radius:12px;
  background-color:rgba(0,192,171,0.5);
  border-width:0px;
  transition: opacity 0.2s;
  height:36px;
  color:white;
  font-weight:bold;
  font-size:15px;
}

.genre_zoom:hover {
  background-color:rgba(0,192,171,0.5);
  opacity:0.6
}

hr {
  margin:5px;
  margin-left:0px;
  margin-right:0px;
}

.genre_group {
  color:#00C0ab;
  font-size:18px;
  font-weight:bold;
}

.group_filter {
  background-color:transparent;
  border:none;
  outline: none;
  color:white;
  width:300px;
  border-bottom:1px solid #aaaaaa;
  transition: border-bottom 0.2s;
  font-size:18px;
}

.group_filter:focus{
  background-color:transparent;
  border:none;
  outline: none;
  border-bottom:1px solid #ffffff;
}

.search_icon {
  cursor: pointer;
  cursor: hand;
  margin-left:8px;
  font-size:16px;
  transition: opacity 0.2s;
}

.search_icon:hover {
  opacity:60%
}

@-webkit-keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-2%);
            transform: translateX(-2%);
    opacity:30%
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity:100%
  }
}

@keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-2%);
            transform: translateX(-2%);
    opacity:30%
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity:100%
  }
}

@-webkit-keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-15%);
            transform: translateY(-15%);
    opacity:0%
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity:100%
  }
}

@keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-15%);
            transform: translateY(-15%);
    opacity:0%
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity:100%
  }
}


.accordion_toggle {
  background-color:rgba(0,192,171,0.2);
  cursor: pointer;
  cursor: hand;
  height: 48px;
}

.accordion_collapse {
  background-color:rgba(0,192,171,0.1);
  border-radius: 0px 0px 3px 3px;
}

.date_switch {
  width:120px;
  border-radius:12px;
  background-color:rgba(255,255,255,0.25);
  border-width:0;
  margin-right:8px;
  transition: opacity 0.2s;
  height:64px;
  color:white;
  font-weight:bold;
  font-size:18px;
}

.timeline {
  overflow: auto;
  white-space: nowrap;
}

.stake {
  height:240px; 
  width:0px;
  background-color:transparent;
  border-right:2px solid rgba(255,255,255,0.5);
}

.stake_container {
  width:30px;
  margin-left:80px;
  margin-right:80px;
}

.stake_text {
  color:white;
  font-weight:bold
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

